import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {

    path: '/',
    name: 'home',
    component: HomeView
  },
  
  {
    path: '/hello',
    name: "HelloWorld", 

    component: () => import('../components/HelloWorldq.vue'),
    children: [{
      path: 'LeftList',
      name: 'LeftList',
      component: () => import('../components/LeftList.vue'),
      children: [{
        path: '/about',
        name: 'about',
        component: () => import('../views/AboutView.vue')
      },]
    }]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
